.section
{
padding: 3rem 0;
}

.grid
{
  display: grid;
  gap: 9rem 0;
}

.grid-two-column
{
  grid-template-columns: 1fr 1fr;
}

.grid-three-column
{
  grid-template-columns: repeat(3,1fr);
}
.grid-four-column
{
  grid-template-columns: repeat(4,1fr);
}
.common-heading
{
  font-size: 2.5rem;
  font-weight: 600;
  margin-bottom: 6rem;
  text-align: center;
  position: relative;
}

.common-heading::before
{
  content: "";
  position: absolute;
  top: 105%;
  left: 0;
  width: 1.5rem;
  height: 1.5rem;
  border-radius: 50%;
  background:rgba(230, 141, 207);

}
.common-heading::after
{
  content: "";
  position: absolute;
  top: 125%;
  left: 1rem;
 min-width: 30rem;
  height: 0.3rem;
  background:rgba(230, 141, 207);
  

}

.button
{
 background-image: linear-gradient(to right, rgb(160, 184, 221), rgb(230, 141, 207)); 
 width: 30%;
padding: 0.5rem;
padding-left: 1.8rem;
 border: none;
 border-radius: 10%;
 display: inline-block;

 text-transform: uppercase;
 font-size: 1rem;
 font-weight: bold;
 cursor: pointer;
 transition: all 0.3s ease;
 -webkit-transition: all 0.3s ease ;
 -moz-transition: all 0.3s ease;
 -o-transition: all 0.3s ease;
}


.button:hover, .button:active
{
  box-shadow: 0 2rem 2rem rgb(132, 144, 255/30%);
}
