.bio-progress-bar
{
width: 90%;
height:0.8rem;
background-color: rgb(202, 194, 194);
border-radius: 10rem;
position: relative;
box-shadow: 0 0.2rem 0.2rem 0 rgb(202, 194, 194);;
}

.bio-progress-bar::before
{
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  /* width: 80%; */
  height: inherit;
  background-image: linear-gradient(to right, rgb(160, 184, 221), rgb(230, 141, 207)); 
  border-radius: 10rem;
}
.bio-progress-1::before
{
  width: 85%;
}
.bio-progress-2::before
{
  width: 80%;
}
.bio-progress-3::before
{
  width: 70%;
}
.bio-progress-4::before
{
  width: 60%;
}
.bio-progress-5::before
{
  width: 70%;
}
.bio-progress-6::before
{
  width: 75%;
}
.bio-progress-bar span{
  position: absolute;
  top: 1.8rem;
  width: 2.7rem;
  height: 1.4rem;
  /* background-color: rgb(248, 136, 248); */
  background-image: linear-gradient(to right, rgb(160, 184, 221), rgb(230, 141, 207)); 
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  border: none;
  outline: none;
  box-shadow:  0  0.3rem 1rem 0 plum;

}

.bio-progress-bar span
{
  left: 80%;
}
.bio-progress-2 span{
  left: 75%;
}
.bio-progress-3 span{
  left: 65%;
}
.bio-progress-4 span{
  left: 55%;
}
.bio-progress-5 span{
  left: 65%;
}
.bio-progress-6 span{
  left: 70%;
}
/* to create a caret on square */
.bio-progress-bar span::after
{
content: "";
position: absolute;
width: 0;
height: 0;
border: 0.8rem  solid plum;
border-color: transparent;
border-bottom-color: plum;
top: -1.5rem;
}

